#Name{
  user-select: none;
}

#inviteText{
  user-select: none;
}
.nav-item{
  user-select: none;
}

body {
  background-color: #08161f;
  /* background-color: #0e2431; */
}

body.dark {
  background-color: #000000;
}

p {
  color: white;
  padding: 0 1rem 0 1rem;
  font-family: "Mulish", sans-serif;
}

a {
  text-decoration: none;
  color: #2ecc71;
  font-family: "Mulish", sans-serif;
}


#mainlogo{
  font-size: 1.3em;
  font-weight: 700;
  font-family: "Mulish", sans-serif;
}




@media screen and (min-width: 480px) {

.navbarPhone{
    display: none;
  }

.navbar {
  height: 3.5rem;
  width: 80vw;
  background-color: #18202c;
  display: flex;
  margin: 2em auto;
  justify-content: space-between;
  align-items: center;
  border-radius: 1rem;
  padding: 0 1rem 0 2rem;
}

.body-container {
  width: 80vw;
  height: 80vh;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translate(0, -10%);
}

#inviteDesc {
  flex-flow: column;
}

#Name {
  font-size: 9em;
  font-weight: 800;
  font-family: "Mulish", sans-serif;
  color: #ffffff;
  text-align: center;
}

#NameDesc {
  font-size: 1.5rem;
  font-weight: 300;
  font-family: "Mulish", sans-serif;
  color: #ffffff;
}

#logocontainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

#mainlogo {
  padding: 10px;
}

#darkbutton {
  height: 2rem;
  width: 2rem;
  background-color: #ffffff;
  border-radius: 50%;
}


#buttoncenter{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 5rem;
    transform: translate(-5%, 10%);
    animation-name: invitedancerweb;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-direction: alternate-reverse;
}
#buttoncenter:hover{
    animation-play-state: paused;
}

#inviteText{
    font-size: 3rem;
    font-weight: 900;
    padding: 10px;
    color: #0e2431;
    font-family: 'Rubik', sans-serif;
}


}

@keyframes invitedancerweb {
    from {

    transform: translate(-5%, 0%);
    }
    to{
        
    transform: translate(-5%, 30%);
    }
}


@keyframes invitedancerPhone {
  from {

  transform: translate(0, 10%);
  }
  to{
      
  transform: translate(0,50%);
  }
}


@media screen and (max-width: 480px) {
      
      .navbar {
        display: none;
      }

      .navbarPhone{
        display: flex;
        height: auto;
        width: auto;
        padding: 0 35px ;
        font-size: 1.5rem;
        font-weight: 300;
        font-family: "Mulish", sans-serif;
        color: #ffffff;
      }
      
      .body-container {
        width: 80vw;
        height: 80vh;
        margin: 0 auto;
        display: flex;
        align-items: center;
        flex-direction: column;
      }
      
      #inviteDesc {
        padding: 4em 0 0 0;
        flex-flow: column;
        align-self: center;
      }
      
      #Name {
        font-size: 3.5em;
        font-weight: 800;
        padding: 0.8em 0 0 0;
        text-align: center;
        font-family: "Mulish", sans-serif;
        color: #ffffff;
      }


      #NameDescBox{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 3em 0 0 8vw;
        flex-flow: column;
        align-self: center;
      }

      
      #NameDesc {
        font-size: 1.5rem;
        font-weight: 300;
        font-family: "Mulish", sans-serif;
        color: #ffffff;
      }
      
      #logocontainer {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      
      #mainlogo {
        padding: 10px;
      }

      
      #buttoncenter{
          display: flex;
          justify-content: center;
          align-items: center;
          padding-bottom: 5rem;
          animation-name: invitedancerPhone;
          animation-duration: 1s;
          animation-iteration-count: infinite;
          animation-timing-function: ease-in-out;
          animation-direction: alternate-reverse;
      }

      #inviteText{
          font-size: 2rem;
          font-weight: 900;
          padding: 3px;
          color: #0e2431;
          font-family: 'Rubik', sans-serif;
      }
}